/*
 * @name: js\utils\andonTypeDrawstring.js
 * @author: EVUN
 * @modify: kelichao
 * @update: 2017-06-13
 * @decript: 
 * @url: http://git.evun.cn/GMES/GMES_CLIENT_PAD
 */

var security = require("security");
var kit = require("kit");
var util = require('util');

var andonPullCordsMultilevelOperateCtrl = {
	STATE_TOUCH: 1,
	STATE_NORMAL: 3,
	page: null,
	station: undefined,
	init: function () {

		var vueObj = new vue({
			data: {
				nowSecGroupAndonCode: "",
				timeContainer: null,// 
				showAll: true,// 
				mainContainer: null,// 
				secContainer: null,// 
				station: {
					stationCode: "",
					stationText: ""
				},
				workStationList: [
					// { stationText: "--", stationCode: "--"} // 
				],
				nowWorkStation: "--",// 
				singleLevel: false,// 
				// 
				pullAndon: {
					// normalColor: "",// 
					// touchColor: "",// 
					// status: "",	/ /andon,3
					// touchable: true,// 
					// touchedTime: '2017-08-08 23:32:33',// andonnull
					// andonText: "",// andon
					// andonCode: "QTYCAN"// andon
				},
				// 
				mainModule: [
					// {
					// 	andonCode: "CSJD1_ZZC_TL01_SB",//
					// 	andonText: "Andon", // 
					// 	childAndonCount: 2,// 
					// 	childAndonTouchedCount: 2,// 
					// 	extendFeatures: null,// 
					// 	normalColor: "#00FF00",// 
					// 	touchColor: "#FF0000",// 
					// 	status: "3",// andon,3,1,2
					// 	touchAble: false, // 
					// 	touchedTime: null// 
					// }
				],
				// 
				secModule: [
					// {
					// 	"normalColor" : "",
					// 	"touchColor" : "",
					// 	"status" : "",// andon,3,1,2
					// 	"touchAble" false,
					// 	"touchedTime" : 100,
					// 	"andonText" : "",
					// 	"andonCode" : "QTYCAN",
					// 	"extendFeatures" : [{
					// 		"code" : ""
					// 	}]
					// }
				],
				isMainShow: true
			},
			// 
			mounted: function () {
				// 
				util.setBreadcrumb([""]);
				this.init();
				this.timeContainer = setInterval(() => {
					this.changeOpenTime();
				}, 1000);

				// page
				myApp.onPageBeforeRemove("pcs_sopWorkInstruction", () => {
					clearInterval(this.timeContainer);
				});
			},
			// 
			filters: {
				setFontSize: function(text) {
					var length = text.length;
					var fontSize = "36px";
					if (length > 5 && length <15) {
						fontSize = "22px";
					} else if (length > 15) {
						fontSize = "18px";
					}
					return {fontSize: fontSize};
				}
			},
			// 
			computed: {
				compStation: function () {
					return this.nowWorkStation.stationText;
				},
				compMainModule: function () {
					setTimeout(() => {
						if (vueObj.mainContainer) {
							vueObj.mainContainer.cleanupStyles();// 
						}

						vueObj.initMainSwiper();
					}, 0);
					return this.createArrayGroup(this.mainModule);
				},
				compSecModule: function () {
					// 
					setTimeout(() => {
						if (vueObj.secContainer) {
							vueObj.secContainer.cleanupStyles();// 
						}

						vueObj.initSecSwiper();
					}, 0);
					return this.createArrayGroup(this.secModule, this.showAll);
				}
			},
			// 
			el: "#andonPullCordsMultilevelOperate",
			// 
			methods: {
				formatFunction: function(value) {
					var theTime = parseInt(value);// 
					var theTime1 = 0;// 
					var theTime2 = 0;// 
					if(theTime > 60) {
						theTime1 = parseInt(theTime/60);
						theTime = parseInt(theTime%60);

						if(theTime1 > 60) {
							theTime2 = parseInt(theTime1/60);
							theTime1 = parseInt(theTime1%60);
						}
					}
						var result = ""+parseInt(theTime);

						if(theTime1 > 0) {
							result = ""+parseInt(theTime1)+":"+result;
						} else {
							result = "00:"+result;
						}

						if(theTime2 > 0) {
							result = "" + parseInt(theTime2)+":"+result;
						} else {
							result = "00:"+result;
						}
					return result;
				},
				changeOpenTime: function() {
					var data = [];
					if (this.isMainShow) {
						data = this.mainModule;
					} else {
						data = this.secModule;
					}

					kit.forEach(data, (value, key) => {
						if (data[key].status == 1) {
							var second  = this.cerateSecond(data[key], 1);
							data[key].isMiddleColor = second >= 10 ? false : true;
							data[key].finalTime = this.formatFunction(second);
						}
					});
				},
				formatSeconds: function(data) {

					kit.forEach(data, (value, key) => {
						if (data[key].status == 1) {
							var second  = this.cerateSecond(data[key], 0);
							data[key].isMiddleColor = second >= 10 ? false : true;
							data[key].finalTime = this.formatFunction(second);
						}
					});
					return data;
				},
				cerateSecond: function(value, add) {
					var second  = (security.getServerDateTime() - value.touchedTime)/1000;
					second += add || 0;
					return second;
				},
				setBackground: function(item) {
					var _color = "linear-gradient(#ADB1BD, #586382)!important";
					var cursor = "";
					if (item.touchAble) {
						if (item.isMiddleColor) {
							_color = "rgb(233, 130, 45)";
						} else {
							if (item.status === "3") {
								//  // 
								if (item.childAndonCount && item.childAndonTouchedCount) {
									_color = item.touchColor;
								} else {
									_color = item.normalColor;
								}
								
							} else if (item.status === "1") {
								_color = item.touchColor;
							}
						}

					} else {
						cursor = "not-allowed";
					}
					return {background: _color, cursor: cursor};
				},
				// 
				init: function () {
					this._getWorkStation();
				},
				// 6
				createArrayGroup: function (arr, flag) {
					var result = [];

					if (arguments.length > 1 && !flag) {
						arr = arr.filter(function (item) {
							if (item.status == "1" && item.touchAble === true) {
								return true;
							}
						});
					}

					for (var i = 0; i < arr.length; i += 6) {
						result.push(arr.slice(i, i + 6));
					}
					return result;
				},
				// 
				andonOperate: function (item) {
					util.service("GMES-CLIENT-ANDON016", { 
						stationCode:  this.nowWorkStation.stationCode,
						andonCode: item.andonCode,
						currentStatus: item.status
					}, "")
						.done((resp) => {
							var andonCode = item.andonCode;
							var status = resp.data.status;
							if (andonCode !== status) {
								this._getSecModule(this.nowSecGroupAndonCode);
							}
						});
				},
				// 
				showAllEvent: function (flag) {
					this.showAll = flag;
					setTimeout(() => {
						this.secContainer.cleanupStyles();// 
						this.initSecSwiper();
					}, 0);
				},
				changeStation: function (item) {
					this.nowWorkStation = item;
					this.isMainShow = true;
					this._getMainModule();
				},
				returnIndex: function() {
					this.isMainShow = true;
					this._getMainModule();
				},
				triggerSwitch: function () {
					util.service("GMES-CLIENT-ANDON016", { 
						stationCode:  this.nowWorkStation.stationCode,
						andonCode: this.pullAndon.andonCode,
						currentStatus: this.pullAndon.status
					}, "")
						.done(() => {
							notie.alert({type:1, text: ""});
							this._getMainModule();
						});
				},
				// 
				initMainSwiper: function () {

					this.mainContainer = myApp.swiper(".main-wrapper.swiper-container", {
						pagination: ".main-wrapper .swiper-pagination",
						paginationHide: false,
						onlyExternal: true,
						paginationClickable: true,
						nextButton: ".main-wrapper .swiper-button-next",
						prevButton: ".main-wrapper .swiper-button-prev",
					});
					// myswiper.slideTo(0);
				},
				// 
				initSecSwiper: function () {
					this.secContainer = myApp.swiper(".sec-wrapper.swiper-container", {
						pagination: ".sec-wrapper .swiper-pagination",
						paginationHide: false,
						onlyExternal: true,
						paginationClickable: true,
						nextButton: ".sec-wrapper .swiper-button-next",
						prevButton: ".sec-wrapper .swiper-button-prev",
					});
				},
				switchToSec: function (item) {
					this.isMainShow = false;

					if (this.secContainer) {
						this.secContainer.cleanupStyles();// 
					}

					// 
					setTimeout(() => {
						this.nowSecGroupAndonCode = item.andonCode;
						this._getSecModule(item.andonCode);
					}, 0);

				},
				_getSecModule: function(andonCode) {

					this.secModule = [];

					util.service("GMES-CLIENT-ANDON015", {
						andonCode: andonCode,
						stationCode: this.nowWorkStation.stationCode,
					}, "")
						.done((resp) => {
							util.existData(resp, (resp) => {
								this.secModule = this.formatSeconds(resp.data);
							});
						});
				},
				// 
				_getMainModule: function () {

					this.mainModule = [];

					util.service("GMES-CLIENT-ANDON014", {
						stationCode: this.nowWorkStation.stationCode,
					}, "")
						.done((resp) => {
							util.existData(resp, (resp) => {

								var module = this.formatSeconds(resp.data.commomAndonList);
								// 
								if (resp.data.singleLevel) {
									this.singleLevel =  true;
									this.isMainShow = false;
									this.secModule = module;
								
								// ,
								} else {
									this.singleLevel =  false;
									this.isMainShow = true;
									this.mainModule = module;
								}

								//
								this.pullAndon = resp.data.pullAndon;
							});
						});
				},
				// 
				_getWorkStation: function () {
					var arr =[];
					kit.forEach(security.getTerminalStations().stations, function(value, key) {
						arr.push({
							stationCode: key,
							stationText: value
						});
					});
					this.workStationList = arr;

					if (this.workStationList.length) {
						this.nowWorkStation = this.workStationList[0];
						this._getMainModule();
					} else {
						notie.alert({ text: "" });
					}
				},
				createPopover: function(event, extendFeatures) {
					var clickedLink = event.target;
					var html = "";

					kit.forEach(extendFeatures, function(value) {
						if (value === "signIn") {
							html +='<li><a href="#" class="item-link list-button andon-check-in f-fsi20"></li>';
						}

						if (value === "stopLine") {
							html += '<li><a href="#" class="item-link list-button andon-pasue f-fsi20"></li>';
						}
						
						if (value === "messageUp") {
							html += '<li><a href="#" class="item-link list-button andon-message f-fsi20"></li>';
						}
						
					});

					var popoverHTML =
						`<div class="popover andon-popover">
							<div class="popover-inner">
								<div class="list-block">
								<ul>`
								 + html +
								`</ul>
								</div>
							</div>
						</div>`;
					myApp.popover(popoverHTML, clickedLink);
				},
				checkIn: function(andonCode) {
					$(".andon-check-in").click(function () {
						myApp.closeModal();
						myApp.openPanel('left');

						new vue({
							template:
							`<div class="left-cont">
								<div class="list-block andonPullCordsMultilevelOperate">
									<div class="content-block-title f-fwbi"></div>
									<ul>
										<template v-for="item in list">
											<li @click="selectName(item)">
												<a href="#" class="item-link item-content">
													<div class="item-inner">
														<div class="item-title">{{item.userText}}</div>
													</div>
												</a>
											</li>			
										</template>
									</ul>
								</div>
							</div>`,
							el: ".left-cont",
							data: {
								list: [
									// {userText: "--",userCode: "--"}
								]
							},
							mounted: function() {
								this._getUserList();
							},
							methods: {
								_getUserList: function() {
									util.service("GMES-CLIENT-ANDON017", {
										stationCode: vueObj.nowWorkStation.stationCode,
										andonCode: andonCode
									}, "")
										.done((resp) => {
											util.existData(resp, (resp) => {
												this.list = resp.data;
											});
										});
								},
								selectName: function (item) {
									myApp.confirm("" + item.userText + "", "", function () {
										util.service("GMES-CLIENT-ANDON018", {
											stationCode: vueObj.nowWorkStation.stationCode,
											andonCode: andonCode,
											userCode: item.userCode
										}, "")
											.done((resp) => {
												notie.alert({ type: 1, text: "" });
											});
										
									});
								}
							}
						});
					});
				},
				pasue: function(andonCode) {
					$(".andon-pasue").click(function () {
						myApp.closeModal();
						myApp.openPanel('left');
						new vue({
							template:
							`<div class="left-cont">
								<div class="list-block andonPullCordsMultilevelOperate">
									<div class="content-block-title f-fwbi"></div>
									<ul>
										<template v-for="item in list">
											<li @click="selectTime(item)">
												<a href="#" class="item-link item-content">
													<div class="item-inner">
														<div class="item-title">{{item.stopLineTimeSt}}-{{item.stopLineTimeEd}}</div>
													</div>
												</a>
											</li>			
										</template>
									</ul>
								</div>
							</div>`,
							el: ".left-cont",
							data: {
								list: [
									// {id : "ID",stopLineTimeSt : "",stopLineTimeEd : ""}
								]
							},
							mounted: function() {
								this._getTimeList();
							},
							methods: {
								_getTimeList: function() {
									util.service("GMES-CLIENT-ANDON019", {
										stationCode: vueObj.nowWorkStation.stationCode,
										andonCode: andonCode
									}, "")
										.done((resp) => {
											util.existData(resp, (resp) => {
												this.list = resp.data;
											});
										});
								},
								selectTime: function (item) {
									myApp.confirm("" + item.stopLineTimeSt + "-" + item.stopLineTimeEd + "" + item.messageReceive || "--", "", function () {
										util.service("GMES-CLIENT-ANDON020", {
											stationCode: vueObj.nowWorkStation.stationCode,
											andonCode: andonCode,
											deployId: item.id
										}, "")
											.done((resp) => {
												notie.alert({ type: 1, text: "" });
												// util.existData(resp, (resp) => {
												// 	this.list = resp.data;
												// });
											});
									});
								}
							}
						});
					});
				},
				messageUp: function(andonCode) {
					$(".andon-message").click(function () {
						myApp.closeModal();
						myApp.confirm("", "", function () {

							util.service("GMES-CLIENT-ANDON021", {
								stationCode: vueObj.nowWorkStation.stationCode,
								andonCode: andonCode
							}, "")
								.done((resp) => {
									notie.alert({ type: 1, text: "" });
									// util.existData(resp, (resp) => {
									// 	this.list = resp.data;
									// });
								});
						});
					});
				},
				showOperate: function (event, item) {
					var andonCode = item.andonCode;
					var extendFeatures = item.extendFeatures[0];// 
					this.createPopover(event, extendFeatures);// 
					this.checkIn(andonCode);// 
					this.pasue(andonCode);// 
					this.messageUp(andonCode);// 
				}
			}
		});
	}
};

module.exports = andonPullCordsMultilevelOperateCtrl;




